import React from 'react'

const TableHead = () => {
    return ( <thead>
        <tr>
          <th>Nombre</th>
          <th>Oficio</th>
          <th>Borrar</th>
        </tr>
      </thead> );
}


const TableBody = (props) => {
  console.log(props)
    return ( <tbody>
        <tr>
          <td>Rosinni</td>
          <td>Profesora</td>
        </tr>
        <tr>
          <td>Ignacio</td>
          <td>Estudiante</td>
        </tr>
      </tbody> );
}
 

function Table() {
    return (
        <table>
            <TableHead/>
            <TableBody/>
        </table>
    )
}

export default Table
